.seccion-contactar {
  position:relative;
width:100vw;
height:100vh;

display: flex;
    justify-content: center;
    align-items: center;
   z-index:10;
}
.contenedor-contactar {
position :relative;
width:95vw;
height:80vh;
box-shadow: 1px 1px 20px 10px rgb(74,158,255,0.3);
display: flex;
overflow: hidden;
}


.contenedor-1 {
position :relative;
width:30%;
height:100%;
background:linear-gradient(-40deg,rgb(62,80,193,1) ,rgb(37,129,217,1)  );
display:flex;
justify-content: center;
align-items: center;

}



.contenido-1-izquierdo {
position: relative;

width: 90%;
height: 90%;
display: flex;
justify-content: center;
align-items: center;
flex-wrap: wrap;

border-bottom-right-radius: 22vw;
}



.contenido-1-izquierdo p {
position: relative;
width: 90%;
margin: 0vw 0vw 0vw 0vw;

}


.titulo-metodous {
position:relative;
font-size: 2.3vw;
font-weight: 100;
margin: 0vh 0vh 0vh 0vh;
color: aqua;
padding: 0vw 0vw 0vw 0vw;
text-align: center;
font-family: 'Dancing Script' , cursive;  
}


.comentario {
font-family: Segoe UI Symbol;
font-size: 1.1vw;
color: white;
margin: 1.5vh 0vh 1.5vh 0vh;
}


.ListaMetodoDeTrabajo {
font-family: Segoe UI Symbol;
font-size: 1.1vw;
background: rgb(29, 146, 226, 0.4);
color: white;
border-left: 0.2vw solid rgb(29, 146, 226, 1);
border-bottom: 0.2vw solid rgb(29, 146, 226, 1);
padding: 1.5vh 0vh 1.5vh 1.5vh;
}

.contenedor-2 {
position :relative;
width:35%;
height:100%;
display: flex;
justify-content: center;
align-items: center;
background: rgb(0,0,0,0.1);

}



.contenedor-form {
position: absolute;
width:max-content;
height:max-content;
background-size: cover;
box-shadow: 0px 6px 6px 4px rgb(0,0,0,0.2);
display: flex;
justify-content: center;
align-items: center;
flex-wrap: wrap;


border-radius: 5%;
background:crimson; 
background:linear-gradient(-40deg,rgb(62,80,193,1) , rgb(29,146,226,1));



}





form {
position: relative;
width: 25vw;
height:73vh;   
display: flex;
justify-content: space-around;
border-radius: 5%;
flex-wrap: wrap;


}


.titulo-contactarme {
position: relative;
width: 100%;
margin: 0vw 0vw 0vw 0vw;
padding:1vh 0vh 1vh 0vh; 
font-size: 2.3vw;
color: white;
display: flex;
justify-content: center;
align-items: center;
font-family: 'Dancing Script' , cursive; 
  
}

.titulo-contactarme:before {
content: "";
position: absolute;
width: 45%;
height: 3px ;
background:rgb(253,200,1);
bottom:10px;
}


label {
width: 90%;
font-size: 1.2vw;
color: white;
font-family: Segoe UI Symbol;
padding:0vh 0vh 0vh 0vh; 

}



input,
textarea  {
background:white;
width:90%;
height: 4.3vh;
margin: 1vh 0vh 2vh 0vh;
font-size: 1.2vw;
font-family: Segoe UI Symbol;
color: rgb(45,45,45);
border: 0.45vh solid rgb(253,200,1);
box-shadow: 5px 7px 10px 4px rgb(0, 0, 0, 0.2);
border-radius: 1vw;
outline: none;

}


textarea {
height: 30vh;
}




button {
width: 100%;
height: 10%;
  margin-top: 2vh;
font-size: 2vw;
color: white;
background:orange;
border-radius: 0.5vw; 
border:none;
box-shadow: 2px 4px 6px 2px rgb(0,0,0,0.2);
font-family: Segoe UI Symbol;
cursor: pointer;

}

button:hover {
  background:linear-gradient(120deg,orange,darkorange);
}

.espacial {
  position: absolute;
  right: 10%;
width: 15%;
height: 67%;
top:16.5%;  
background: url("../gallery/nave2.png");
background-size: cover;


}

.espacial p {
  position: relative;
 right: 30%;
 top:45%;
background:  linear-gradient(-40deg,rgb(62,80,193,0.9) ,rgb(37,129,217,0.9) );
color:aqua;
padding: 1vh;
border-radius: 1vw;
font-size:1.05vw ;
}


.espacial p i {
position: absolute;
padding: 0.2vh 1vh 0.2vh 1vh;
left:-2.5vh;
top:-1.5vh;
color:yellow;
background: rgb(37,129,217,0.9);
border-radius: 50%;
border-bottom: 0.4vh solid yellow;
font-size: 1.2vw;
}



.contenedor-botones{
position:absolute;

right: 23%;
width: 9vw;
height: 7vh;

border-bottom-left-radius: 1vw; 
 display: flex;
   justify-content: center;
   align-items: center;
background:linear-gradient(-40deg,rgb(62,80,193,1) , rgb(29,146,226,1));
box-shadow: 1px 5px 6px 3px rgb(0,0,0,0.2);

}




 .botones {
position: relative;
width: 95%;
height: 80%;

box-shadow: 0px 0px 6px 3px rgb(0,0,0,0.2);
border-bottom-left-radius: 1vw;
 display: flex;
   justify-content:space-around;
   align-items: center;
   overflow: hidden;
   

}


.botones:before {
content: "";
position: absolute;
width: 100%;
height: 100%;

background:linear-gradient(rgb(8,8,95),rgb(6,6,102,0.8),rgb(8,8,95)); 
clip-path: polygon(60% 0, 100% 0, 100% 100%, 40% 100%);


}



.botones button {
position: relative;
width:2.2vw;
height: 2.2vw; 
border-radius: 50%;
display: flex;
justify-content: center;
align-items: center;
background:rgb(255,255,255,0.1); 
border: 2px solid rgb(240,240,240,0.7);
box-shadow: 1px 1px 4px 2px rgb(0,0,0,0.2);
margin: 0vh 0vh 0vh 0vh;
cursor: pointer;

}

.botones button:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(255, 255, 255, 0);
}


 .botones button i{  
  color:white; 
  font-size: 1.2vw;

}

  

